.main-nav__toggle,
.main-nav__toggle-trigger,
.main-nav__toggle-burger {
  width: 30px;
  height: 30px;
  position: absolute;
  top: 17px;
  right: 17px;
  transition: 0.2s ease;
  cursor: pointer;
  z-index: 1; }

.main-nav__toggle-burger {
  top: 29px;
  right: 28px;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: #1c1c1c;
  box-shadow: -12px 0 #1c1c1c, 12px 0 #1c1c1c, 12px 12px #1c1c1c, -12px -12px #1c1c1c, 12px -12px #1c1c1c, -12px 12px #1c1c1c; }
  .main-nav__toggle-burger::before {
    position: absolute;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: #1c1c1c;
    content: "";
    top: -12px;
    left: 0; }
  .main-nav__toggle-burger::after {
    position: absolute;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: #1c1c1c;
    content: "";
    top: 12px;
    left: 0; }

.main-nav__toggle:checked + .main-nav__toggle-trigger + .main-nav__toggle-burger {
  transform: rotate(180deg);
  box-shadow: 12px 12px #1c1c1c, -12px -12px #1c1c1c, 12px -12px #1c1c1c, -12px 12px #1c1c1c;
  transition: transform 0.2s ease; }

.main-nav__toggle:checked + .main-nav__toggle-trigger + .main-nav__toggle-burger::before {
  transform: rotate(45deg) translateX(-5px);
  display: none;
  transition: transform 0.2s ease; }

.main-nav__toggle:checked + .main-nav__toggle-trigger + .main-nav__toggle-burger::after {
  transform: rotate(-45deg) translateX(-5px);
  display: none;
  transition: transform 0.2s ease; }

.main-nav__additional-list {
  display: none;
  -webkit-animation: not-checked-anim 1s both;
          animation: not-checked-anim 1s both;
  transition: 0.2s; }

.main-nav__toggle:checked + .main-nav__toggle-trigger + .main-nav__toggle-burger + .main-nav__additional-list {
  -webkit-animation: checked-anim 1s ease both;
          animation: checked-anim 1s ease both; }

.main-nav__toggle:checked + .main-nav__toggle-trigger ~ .main-nav__additional-list {
  display: block; }

